import React, { useEffect, useRef, useState } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import skillsStyle from "./style/skills.module.css";
import reactImg from "../img/react.png";
import frontEndImg from "../img/frontEnd.png";
import cssImg from "../img/css.png";
import backEndImg from "../img/backEnd.png";
import nodeJsImg from "../img/nodeJs.png";
import databaseImg from "../img/database.png";
import mysqlImg from "../img/mysql.png";
import languageImg from "../img/language.png";
import javaImg from "../img/java.png";
import pythonImg from "../img/python.png";
import jsIcon from "../img/javascript.png";
import htmlIcon from "../img/html.png";
import typescriptIcon from "../img/typescript.png";

const Skills = () => {
  useEffect(() => {
    // AOS 
    AOS.init({
      duration: 1200, //   
      easing: "ease-in-out", //   
      once: true, //     
      mirror: true, //     
    });
  }, []);

  const [isHovered, setIsHovered] = useState(false);
  const [popupPosition, setPopupPosition] = useState({ left: "50%" });
  const popupRef = useRef(null);

  useEffect(() => {
    if (isHovered && popupRef.current) {
      const rect = popupRef.current.getBoundingClientRect();
      const windowWidth = window.innerWidth;

      if (rect.right > windowWidth) {
        setPopupPosition({ left: "auto", right: "0px" }); //    
      } else {
        setPopupPosition({ left: "50%", right: "auto" }); //   
      }
    }
  }, [isHovered]);

  const skillsFront = [
    {
      iconSrc: reactImg,
      skillName: "React",
      description:
        " : 3\n    ,       ",
    },
    {
      iconSrc: htmlIcon,
      skillName: "HTML",
      description:
        " : 3\n      ,       ",
    },
    {
      iconSrc: cssImg,
      skillName: "CSS",
      description:
        " : 3\n HTML     . \n  CSS Modules    ",
    },
    {
      iconSrc: jsIcon,
      skillName: "JavaScript",
      description:
        " : 3\n      \nES6 ,        ",
    },
    {
      iconSrc: typescriptIcon,
      skillName: "TypeScript",
      description:
        " : 1\n     . \n        ",
    },
  ];

  const skillsBack = [
    {
      iconSrc: nodeJsImg,
      skillName: "Node.js",
      description:
        " : 3\n express   , RESTful API    .\n     1:1   .\n MYSQL           .",
    },
  ];

  const skillsDatabase = [
    {
      iconSrc: mysqlImg,
      skillName: "MYSQL",
      description:
        " : 3\n   3  .  ,     . \n JOIN, GROUP BY       . \n   ,     .",
    },
  ];

  const skillsLanguage = [
    {
      iconSrc: javaImg,
      skillName: "JAVA",
      description:
        " : 3\n    3  .       . \n            .",
    },
    {
      iconSrc: pythonImg,
      skillName: "PYTHON",
      description:
        ": 1\n     ,    . ,    .",
    },
  ];

  return (
    <section
      id="skills"
      className={skillsStyle.skillsContainer}
      data-aos="fade-up"
    >
      <div className={skillsStyle.skillsContentBox}>
        <div className={skillsStyle.skillsTitleBox}>
          <div>Skills</div>
        </div>
        <div className={skillsStyle.skillsDetailBox}>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={frontEndImg}></img>
              </div>
              <div>Front-end</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsFront.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={backEndImg}></img>
              </div>
              <div>Back-end</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsBack.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={databaseImg}></img>
              </div>
              <div>Database</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsDatabase.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className={skillsStyle.skillsSkillBox}>
            <div className={skillsStyle.skillsskillTitleBox}>
              <div className={skillsStyle.skillsskillTitleImgBox}>
                <img src={languageImg}></img>
              </div>
              <div>Language</div>
            </div>
            <div className={skillsStyle.skillsskillDetailBox}>
              {skillsLanguage.map((skill, index) => (
                <div className={skillsStyle.skillsskillDetailImgBox}>
                  <img src={skill.iconSrc} alt={`image ${index}`} />
                  <div className={skillsStyle.popupText}>
                    <h4>{skill.skillName}</h4>
                    <p>{skill.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
